import { createRouter, createWebHashHistory } from "vue-router";
import HomePage from "../views/HomePage.vue";
import CourseIntroduction from "../views/CourseIntroduction.vue";
import TeachingPlan from "../views/TeachingPlan.vue";
import TeachingCases from "../views/TeachingCases.vue";
import DesignTopics from "../views/DesignTopics.vue";

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: HomePage,
  },
  {
    path: "/course-introduction",
    name: "CourseIntroduction",
    component: CourseIntroduction,
  },
  {
    path: "/teaching-plan",
    name: "TeachingPlan",
    component: TeachingPlan,
  },
  {
    path: "/teaching-cases",
    name: "TeachingCases",
    component: TeachingCases,
  },
  {
    path: "/design-topics",
    name: "DesignTopics",
    component: DesignTopics,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
